<template>
    <div >
        <div class="main-bn stats-bn">
            <div class="list-name solo">
                <span>
                    <span class="txt">Статистика проекта</span>
                </span>
            </div>
            <img src="@/assets/img/stats-img1.svg" alt="" class="stats-img1">
            <img src="@/assets/img/stats-img2.svg" alt="" class="stats-img2">
            <img src="@/assets/img/stats-img3.svg" alt="" class="stats-img3">
            <img src="@/assets/img/stats-img4.svg" alt="" class="stats-img4">
            <img src="@/assets/img/stats-img5.svg" alt="" class="stats-img5">
            <img src="@/assets/img/stats-img6.svg" alt="" class="stats-img6">
            <div class="big-ticket">
                <div class="text-size-s text-center text-color-yellow mb4">Общая сумма предоставленных скидок</div>
                <div class="text-size-xxl text-bold text-center text-color-yellow mb4">{{ statistic.discountSumFormatted }} Р</div>
                <div class="text-size-s text-center text-color-yellow mb4">{{statistic.date}}</div>
            </div>
            <div class="big-ticket">
                <div class="text-size-s text-center text-color-yellow mb4">Средняя экономия</div>
                <div class="text-size-xxl text-bold text-center text-color-yellow mb4">{{ statistic.averageDiscountSumFormatted }} Р</div>
                <div class="text-size-s text-center text-color-yellow mb4">на каждого покупателя</div>
            </div>
            <div class="stats-circles">
                <div class="circle">
                    <div class="text-size-xxl text-bold text-center text-color-yellow">{{ statistic.countPropvidersFormatted }}</div>
                    <div class="text-size-xs text-color-yellow text-center">
                        поставщик{{declOfNumber(statistic.countPropviders,['','а','ов'])}}
                    </div>
                </div>
                <div class="circle">
                    <div class="text-size-xxl text-bold text-center text-color-yellow">{{ statistic.countActionFormatted }}</div>
                    <div class="text-size-xs text-color-yellow text-center">
                        действующ{{declOfNumber(statistic.countAction,['яя','ие','их'])}} акц{{declOfNumber(statistic.countAction,['ия','ии','ий'])}}
                    </div>
                </div>
                <div class="circle">
                    <div class="text-size-xxl text-bold text-center text-color-yellow">{{ statistic.countCouponsExtinguishedFormatted }}</div>
                    <div class="text-size-xs text-color-yellow text-center">
                        погашенных купон{{declOfNumber(statistic.countCouponsExtinguished,['','а','ов'])}}
                    </div>
                </div>
            </div>
            <div class="text-size-m text-bold text-center text-color-yellow cities-count">
                ОХВАЧЕНО <span class="text-color-white">{{ statistic.countCitiesFormatted }}</span> ГОРОД{{declOfNumber(statistic.countCities,['','А','ОВ'])}} РОССИИ
            </div>
        </div>
        <div class="statistic__map">
            <y-map :scroll-zoom="false" :markers="statistic.coordinates_map"
                   :coords="[55.233870903611,37.439710535057]"
                   :zoom="3"
            ></y-map>
        </div>
    </div>
    <!-- end statistic -->

</template>



<script>
    import {mapState} from 'vuex'
    import YMap from '@/components/Map.vue'
    import {declOfNumber} from '@/helpers/formatted';

    export default {
        metaInfo() {
            return {
                title: 'Статистика проекта',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Statistic",
        components: {
            YMap
        },
        data: function () {
            return {
                sortDirection: 'desc',
            }
        },
        created() {
            this.$store.dispatch('rating/getAllStatistic');
        },
        mounted() {
            ym(39511350, 'hit', this.$route.path, {title: 'Статистика проекта | ZemSkidki.ru'});
        },
        computed: {
            ...mapState({
                statistic:  state => state.rating.statistic,
            }),
        },
        methods: {
            declOfNumber(number, titles){
                return declOfNumber(number, titles);
            },
        }
    }
</script>
