<template lang="html">
  <div>
    <tabs class="docs-tabs" >
      <tab id="tab-tip" name="Типографика">
        <h1>Типографика</h1>
        <p>Весь текст на сайте имеет цвет <code>#545454</code>.<br>Sass переменная <code>$text-color</code></p>
        <p>Дефолтный шрифт: <code>"Proxima Nova", sans-serif, Arial</code> его не нужно нигде указывать, прописан в _vars.sass</p>
        <hr>
        <h2>Выравнивание текста</h2>
        <p class="text-left">Класс <code>text-left</code></p>
        <p class="text-center">Класс <code>text-center</code></p>
        <p class="text-right">Класс <code>text-right</code></p>
        <hr>
        <h2>Цвета текста</h2>
        <p class="text-color">Обычный. Класс <code>text-color</code></p>
        <p class="text-color-red">Красный (ошибка). Класс <code>text-color-red</code></p>
        <p class="text-color-green">Зелёный. Класс <code>text-color-green</code></p>
        <p class="text-color-white">Белый. Класс <code>text-color-white</code></p>
        <p class="text-color-primary">Цвет проекта. Класс <code>text-color-primary</code></p>
        <hr>
        <h2>Стили текста</h2>
        <p><span class="text-normal">Текст обычный.</span> Класс <code>text-normal</code></p>
        <p><span class="text-bold">Текст полужирный.</span> Класс <code>text-bold</code></p>
        <p><span class="text-extrabold">Текст жирный.</span> Класс <code>text-extrabold</code></p>
        <hr>
        <h2>Размеры текста</h2>
        <p><span class="text-size-xs">10px</span>. Класс <code>text-size-xs</code></p>
        <p><span class="text-size-s">12px</span>. Класс <code>text-size-s</code></p>
        <p><span class="text-size-m">14px</span>. Класс <code>text-size-m</code></p>
        <p><span class="text-size-l">18px</span>. Класс <code>text-size-l</code></p>
        <p><span class="text-size-xl">24px</span>. Класс <code>text-size-xl</code></p>
        <p><span class="text-size-xxl">32px</span>. Класс <code>text-size-xxl</code></p>
        <p><span class="text-size-big">56px</span>. Класс <code>text-size-big</code></p>
      </tab>
      <tab id="tab-inp" name="Поля ввода">
        <h1>Поля ввода</h1>
        <input-styled :placeholder="'Обычное поле'" :placeholder-input="'Обычное поле'"></input-styled>
        <input-styled :current-model="editInpt" v-model="editInpt" :is-clear-btn="true" :placeholder="'Поле с кнопкой очистки'" :placeholder-input="'Поле с кнопкой очистки'"></input-styled>
        <textarea-field  placeholder="Textarea"
                         :auto-focus="true"
        ></textarea-field>
      </tab>
      <tab id="tab-btns" name="Кнопки">
        <h1>Кнопки</h1>
        <p>Кнопка это <code>button</code> с классом <code>btn</code> обычно внутри <code>div</code> с классом <code>button-block</code>, его использование удобно для выравнивания кнопки</p>
        <p>Варианты других цветов кнопки задаются классами <code>btn-red</code> и т.д</p>
        <div class="button-block" >
            <button class="btn">Обычная</button>
            <span><code>btn</code></span>
        </div>
        <div class="button-block" >
            <button class="btn btn-icon">
              <svg class="icon">
                  <use xlink:href="@/assets/img/mob-sprite.svg#exit"></use>
              </svg>
              Обычная
            </button>
            <span><code>btn-icon</code>&nbsp;</span>
        </div>
        <div class="button-block" >
            <button class="btn btn-green">Зелёная</button>
            <span><code>btn-green</code>&nbsp;</span>
        </div>
        <div class="button-block" >
            <button class="btn btn-red">Красная</button>
            <span><code>btn-red</code>&nbsp;</span>
        </div>
        <div class="button-block" >
            <button class="btn btn-transparent btn-icon">
              Прозрачная</button>
              <span><code>btn-transparent</code>&nbsp;</span>
        </div>
        <div class="button-block" >
            <button class="btn btn-transparent btn-icon">
              <svg class="icon">
                  <use xlink:href="@/assets/img/mob-sprite.svg#ticket"></use>
              </svg>
              Прозрачная</button>
              <span><code>btn-icon</code></span>
        </div>
        <div class="button-block" >
            <button class="btn btn-disabled">
             Неактивная</button>
            <span><code>btn-disabled</code></span>
        </div>
      </tab>
      <tab id="tab-info-popups" name="Инфо-модалки">
        <h1>Инфо-модалки</h1>
        <p>Требуется подключить компонент PopupInfoBlock <code>@/components/modal/PopupInfoBlock.vue</code><br>+ в popups.js создать новый объект</p>
        <p>*Обязательные поля - :show и :name. Идентично компоненту popup, указываем данные из popups.js</p>
        <span>Пример</span>
        <div class="code">
            &lt;popup-info-block<br>:show=&quot;popup.show&quot;
            <br>
            :title=&quot;&#39;Заголовок&#39;&quot;
            <br>
            :redtitle="true"
            <br>
            :modal-type=&quot;&#39;popupList&#39;&quot;
            <br>
            :text=&quot;&#39;Текст сообщения&#39;&quot;
            <br>
            :greentxt="true"
            <br>
            :btntext=&quot;&#39;Текст у кнопки&#39;&quot;
            <br>
            :name=&quot;popup.name&quot;&gt;
            <br>
            &lt;/popup-info-block&gt;
        </div>
        <p>Значения по умолчанию:</p>
         <p><code @click="clickToCopy()">:title = "''"</code><br>Заголовок</p>
         <p><code>:redtitle = "false"</code><br>Красный заголовок</p>
         <p><code>:greentitle = "false"</code><br>Зелёный заголовок</p>
         <p><code>:modal-type = "''"</code><br>Тип модалки, указываем переменную, для различных вариаций внутреннего контента<br></p>
         <p><code>:text = "''"</code><br>Текст модалки</p>
         <p><code>:redtxt = "false"</code><br>Красный текст</p>
         <p><code>:greentxt = "false"</code><br>Зелёный текст</p>
         <p><code>:btntxt = "'Понятно'"</code><br>Текст кнопки</p>
      </tab>
    </tabs>
  </div>
</template>

<script>
    import { mapState } from 'vuex'
    import Tabs from '@/components/tabs/Tabs.vue';
    import Tab from '@/components/tabs/Tab.vue';
    import Popup from '@/components/modal/Popup.vue';
    import PopupInfoBlock from '@/components/modal/PopupInfoBlock.vue';
    import InputStyled from '@/components/forms/InputStyled.vue';
    import InputMulti from '@/components/forms/InputMulti.vue';
    import InputPrice from '@/components/forms/InputPrice.vue';
    import TextareaField from '@/components/forms/TextareaField.vue';

    export default {
      data(){
        return {
          editInpt: '',
        }
      },
      components:{
        Tabs,
        Tab,
        Popup,
        PopupInfoBlock,
        InputStyled,
        InputMulti,
        InputPrice,
        TextareaField,
      },
      computed: {
          ...mapState({
            popups: state => state.popups.items,
          }),

      },
      methods: {
        clickToCopy: function(that){
          var inp = document.createElement('input');
          document.body.appendChild(inp)
          inp.value =that.textContent
          inp.select();
          document.execCommand('copy',false);
          inp.remove();
        },
      },
    }
</script>
