<template>
  <div class="rating-content rating-block" style="padding-bottom: 0.1px">
    <div class="ttl"><span class="txt">Лучшие акции <br></span><a class="choose-city" @click="showPopup('citySelect')">г.&nbsp;{{currentCity.name}}</a>
    </div>
    <div class="best-suppliers" v-if="ratingList.length > 0">
      <div class="supplier" v-for="(item, index) in ratingList" :key="item.id" :class="{'achive': index < 3}">
        <cup-icon :num="item.position" v-if="item.position > 0 && item.position < 4"></cup-icon>
        <div class="supp-name">{{item.name}}</div>
        <div class="supp-stats">
          <div class="txt">Предоставлено скидок:</div>
          <div class="sum">{{item.sum}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></div>
          <div class="txt">Погашено купонов: {{item.count}}</div>
        </div>
      </div>
    </div>
    <loader v-if="!isLoadedProcess"></loader>
  </div>
</template>

<script>
    import {mapState} from 'vuex';
    import Loader from '@/components/Loader.vue';
    import CupIcon from '@/components/CupIcon.vue'

    export default {
        props: {
        },
        components: {
            Loader,
            CupIcon
        },
        data: function () {
            return {
                sortDirection: 'desc',
                unsubscribe: false,
            }
        },
        created() {
            this.$store.commit('rating/resetPageRating');
            this.$store.dispatch('rating/getActions', {action: 'page'});
        },
        beforeDestroy(){
            if(typeof this.unsubscribe === 'function' ){
                this.unsubscribe();
            }
        },
        mounted() {
            window.addEventListener('scroll', this.handleScroll);
            ym(39511350, 'hit', this.$route.path, {title: 'Рейтинг скидок | ZemSkidki.ru'});
            this.unsubscribe = this.$store.subscribe((mutation, state) => {
                if(mutation.type === 'cities/setCity') {
                    this.$store.dispatch('rating/getActions', {action: 'sort'});
                }
            });
        },
        computed: {
            ...mapState({
                currentCity:  state => state.cities.current,
                isLoadedProcess:  state => state.rating.isLoadedProcess,
                ratingList:  state => state.rating.actions,
                currentProvider:  state => state.rating.currentProvider,
                sort:  state => state.rating.sort.current,
                popups: state => state.popups.items,
            }),
            sort() {
                return this.$store.getters['rating/getSort'];
            }
        },
        methods: {
            setSort: function (sortField) {
                this.$store.dispatch('rating/setSort', {order: sortField});
                this.$store.dispatch('rating/getActions', {action: 'sort'});
            },
            handleScroll: function () {
                let scroll = window.scrollY;
                let viewport = window.innerHeight;
                let bodyHeight = document.body.clientHeight;
                if (bodyHeight - 80 <= scroll + viewport) {
                    this.$store.dispatch('rating/getActions', {action: 'page'});
                }
            },
            activeItem: function (id) {
                this.$store.dispatch('actions/goCoupon', {id:id});
            },
            supplierPopupOpen: function(id){
                this.$store.dispatch('rating/getCurrentProvider', {id: id});
                this.$store.commit('popups/open', {name: this.popups.supplierRating.name});
            },

            showPopup(popup) {
                this.$store.commit('menus/topMenuClose');
                this.$store.commit('popups/open',{name:popup});
            },
        }
    }
</script>
